@import "./vars";

#Header {
  $drawer-width: 300px;

  .drawer>div {
    width: $drawer-width;
    height: calc(100vh - 42px);

    .drawer-content {
      .activated{
        color: #fff;
        background-color: $secondary;
        svg{
          color: #fff;
        }
      }
      .avatar {
        box-sizing: border-box;
        display: block;
        width: 100%;
        height: auto;
        margin: 0px auto;
        border-radius: 0px;
        overflow: hidden;
        box-shadow: #555 0 0 5px;
        & > div{
          margin: 30px auto;
        }
        img {
          display: flex;
          justify-content: center;
          align-items: center;
          width: 100%;
          height: auto;
        }
      }
      .name h1{
        margin: 15px 0;
        font-size: 1.7em;
        font-weight: bold;
      }
      .logo{
        margin: 25px 0;
        display: flex;
        justify-content: center;
        align-items: center;
        img{
          width: 80%;
          height: auto;
          filter: invert(75%) sepia(5%);
        }
      }
      .last-update{
        display: flex;
        justify-content: center;
        align-items: center;
        margin: 20px 0;
        color: #888;
      }
    }
  }

  @media only screen and (max-width: 960px) {
    $drawer-width: 200px;
    
    .drawer>div {
      position: relative;
      width: 100% !important;
      width: $drawer-width;
      height: 100%;

      .drawer-content {
        .avatar {
          width: 200px;
          height: 200px;
          margin: 15px auto;
          border-radius: 100%;
        }
      }
    }
  }
}