@import "./vars";

#Home{
  box-sizing: border-box;
  .more{
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    padding: 10px 30px;
    margin: 5px 0;
    .item{
      margin: 5px 0;
      &.chip{
        margin-bottom: 15px;
        margin-right: 3px;
      }
    }
  }
  .extra{
    margin: 25px 0;
    padding: 25px 30px;
    .extra-title{
      margin-bottom: 20px;
    }
    .extra-item{
      padding-left: 15px;
    }
  }

  @media only screen and (max-width: 960px) {
    .extra{
      .extra-item{
        padding-left: 0;
      }
    }
  }
}