@import './vars';

#Resume{
  box-sizing: border-box;
  position: relative;
  padding: 0 15px;
  display: flex;
  background-color: #fcfcfc;
  height: 100vh;

  .color-primary{
    color: $primary;
  }
  .color-secondary{
    color: $secondary;
  }
  .color-link{
    color: $link;
  }
  
  .top{
    box-sizing: border-box;
    position: absolute;
    top: 25px;
    left: 50%;
    transform: translateX(-50%);
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-evenly;
    align-items: center;
    width: 100%;
    height: 250px;
    background-color: #fcfcfc;
    .avatar{
      overflow: hidden;
      flex-grow: 3;
      text-align: center;
      img{
        width: 175px;
        height: 175px;
        border-radius: 100%;
        border: 11px double $primary;
      }
    }
    .info{
      flex-grow: 5;
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: flex-start;
      .name{
        font-size: xx-large;
        font-weight: 600;
        color: $primary;
      }
      .job{
        font-size: large;
        font-weight: 400;
        color: #6b6b6b;
      }
      .contacts{
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: flex-start;
        margin-top: 15px;
        .contact{
          margin-bottom: 8px;
          &.website{
            color: rgb(39, 167, 129);
            font-weight: 700;
          }
        }
      }
    }
  }

  $hoffset: 275px;
  .left{
    box-sizing: border-box;
    background-color: #3b3b3b;
    width: 40%;
    height: calc(100% + #{$hoffset});
    color: #fff;
    .content{
      box-sizing: border-box;
      position: relative;
      top: $hoffset;
      display: flex;
      flex-direction: column;
      justify-content: flex-start;
      align-items: flex-start;
      padding: 10px 25px;
      h3{
        margin: 13px 0;
      }
      .education{
        width: 100%;
        .item{
          box-sizing: border-box;
          display: flex;
          flex-direction: column;
          justify-content: center;
          align-items: flex-start;
          width: 100%;
          .title{
            display: flex;
            flex-wrap: wrap;
            justify-content: flex-start;
            align-items: center;
            width: 100%;
            font-size: 0.9em;
            margin: 10px 5px;
            margin-top: 0;
          }
          .body{
            display: flex;
            flex-direction: column;
            padding-left: 15px;
            font-size: 0.8rem;
            .date{
              display: flex;
              flex-wrap: wrap;
              justify-content: flex-start;
              align-items: center;
              margin-bottom: 5px;
              svg{
                margin-right: 10px;
              }
            }
            .degree{
              margin-bottom: 5px;
            }
          }
        }
      }
      .experience{
        width: 100%;
        .item{
          box-sizing: border-box;
          display: flex;
          flex-direction: column;
          justify-content: center;
          align-items: flex-start;
          width: 100%;
          margin-bottom: 20px;
          .title{
            display: flex;
            flex-wrap: wrap;
            justify-content: flex-start;
            align-items: center;
            width: 100%;
            font-size: 0.9em;
            margin: 5px 5px;
            margin-top: 0;
          }
          .role{
            font-size: 0.8em;
            color: #aeaeae;
            margin: 10px 5px;
            margin-top: 0;
          }
          .body{
            display: flex;
            flex-direction: column;
            padding-left: 15px;
            font-size: 0.8rem;
            .date{
              display: flex;
              flex-wrap: wrap;
              justify-content: flex-start;
              align-items: center;
              margin-bottom: 5px;
              svg{
                margin-right: 10px;
              }
            }
          }
        }
      }
    }
  }
  .right{
    box-sizing: border-box;
    background-color: #fff;
    width: 60%;
    height: calc(100% + #{$hoffset});
    font-size: 0.85rem;
    .content{
      box-sizing: border-box;
      position: relative;
      top: $hoffset;
      display: flex;
      flex-direction: column;
      justify-content: flex-start;
      align-items: center;
      padding: 5px 20px;
      .skills{
        width: 100%;
        margin-bottom: 15px;
        .skill{
          width: 100%;
          font-size: 10px;
        }
      }
      .projects{
        box-sizing: border-box;
        width: 100%;
        .work{
          width: 100%;
          margin-bottom: 15px;
          font-size: 0.9em;
          .name{
            font-size: 1.1em;
            font-weight: 700;
          }
          .for{
            color: #4b4b4b;
            font-size: 1em;
            font-weight: 500;
            margin-bottom: 5px;
            margin-bottom: 5px;
          }
          .desc{
            font-size: 0.9em;
            margin-bottom: 5px;
          }
        }
      }
    }
  }
}