a{
  text-decoration: none;
  color: inherit;
}

.App {
  box-sizing: border-box;
  display: block;
}

.App-header {
  display: block;
}

.App-main {
  box-sizing: border-box;
  background-color: rgba(237, 237, 237, 0.85);
  min-height: calc(100vh - 52px);
  padding: 20px 35px;
  margin-bottom: 25px;
  width: calc(100% - 300px);
  margin-left: auto;
}

#tsparticles{
  position: fixed;
  top: 0;
  z-index: -1;
  background-color: #ededed;
  width: 100vw;
  height: 100vh;;
}

.App-main.hide {
  width: 100%;
  min-height: unset;
  padding: 0;
  margin: 0;
}

.App-footer {
  display: inline-flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 50px;
  position: fixed;
  bottom: 0;
  z-index: 9999;
  color: #ededed;
  background-color: #2b2b2b;
}

.sweet-loading {
  width: 100vw;
  height: 100vh;
  overflow: hidden;
  display: flex;
  justify-content: center;
  align-items: center;
}

@media only screen and (max-width: 960px){
  .App-main{
    width: 100%;
    padding: 15px 20px;
  }
  .App-footer{
    height: 40px;
  }
}