@import "./vars";

#Projects {
  display: block;
  width: 100%;
  // padding: 25px 50px;
  font-family: cursive;
  box-sizing: border-box;
  overflow: hidden;

  .title {
    color: #2b2b2b;
    font-family: Arial, Helvetica, sans-serif;
  }

  .gold{
    font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
    font-weight: bold;
    color: #000;
    text-shadow: 0 0 1px #000;
  }

  .info {
    display: inline-block;
    width: 100%;
    box-sizing: border-box;
    margin-left: 50px;

    &>div {
      display: inline-block;
      width: 100%;
      box-sizing: border-box;

      .info-flex {
        display: flex;
        align-items: center;
        justify-content: flex-start;
        flex-wrap: wrap;
        margin-bottom: 8px;

        svg {
          padding-right: 10px;
        }
      }

      .info-date {
        color: #666;
        font-family: 'Courier New', Courier, monospace;
      }

      .info-content {
        display: block;
        box-sizing: border-box;
        margin-left: 30px;
        padding: 15px 0;

        &>div {
          display: inline-block;
          width: 100%;
          box-sizing: border-box;
          padding: 10px 0px;
          margin-bottom: 15px;
          border-bottom: 1px solid #ededed;

          .content-top {
            font-family: Arial, Helvetica, sans-serif;

            & > div{
              margin-top: 5px;
              margin-right: 5px;
            }
            
            svg {
              color: #333;
            }
            
            .star {
              color: goldenrod;
              margin-top: 5px;
            }
          }

          .content-middle {
            color: #5b5b5b;
          }

          .content-bottom {
            display: flex;
            flex-wrap: wrap;
            align-items: center;
            line-height: 1.5rem;
            color: #777;

            div {
              box-sizing: border-box;
              display: inline-block;
              word-break: break-word;
              white-space: pre-wrap;
              font-size: 0.95rem;
              font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
            }

            a {
              display: inline-block;
              text-decoration: none;
              color: $link;
              margin-left: 10px;
            }
          }
        }
      }
    }
  }

  @media only screen and (max-width: 960px) {
    .info {
      margin-left: 10px;

      &>div {
        .info-content {
          margin-left: 20px;
        }
      }
    }
  }
}