@import "./vars";

#Skills {
  box-sizing: border-box;
  display: block;
  width: 100%;
  margin: 10px 0;
  padding: 0 0px;
  overflow: hidden;

  .excerpt{
    .excerpt-item{
      margin: 5px;
    }
  }

  .all {
    box-sizing: border-box;
    display: block;

    .skillbar{
      margin-bottom: 10px;
      height: 35px;
      .skillbar-title{
        display: flex;
        justify-content: center;
        align-items: center;
        background-color: rgba($color: #2b2b2b, $alpha: 0.05);
        border-radius: 5px;
      }
      
      .skillbar-bars{
        width: 100%;
        padding: 0 0 0 11px;
        .mini-bar{
          box-sizing: border-box;
          display: inline-block;
          width: calc(100% / 5);
          padding: 0 2.5px;
        }
      }
    }

    &>div {
      display: inline-block;
      width: 100%;
      box-sizing: border-box;

      .CircularProgressbar-background{
        fill: $primary !important;
      }

      .all-title {
        box-sizing: border-box;
        display: flex;
        align-items: center;
        justify-content: flex-start;
        flex-wrap: wrap;
        margin-bottom: 5px;
  
        svg {
          padding-right: 10px;
        }
        font-size: 1.2rem;
        margin-bottom: 15px;
        .info-icon{
          color: $link;
        }
      }
    }
  }

  @media only screen and (max-width: 960px) {
    padding: 0px;
  }
}