@import "./vars";

#Background {
  box-sizing: border-box;

  .data {
    box-sizing: border-box;
    margin: 15px 0;
    padding: 20px 25px;

    .data-item {
      box-sizing: border-box;
      padding-left: 15px;

      .work {
        padding: 5px 15px;

        .url {
          display: flex;
          align-items: center;
          flex-wrap: wrap;
          margin: 10px 0;
          font-family: Verdana, Geneva, Tahoma, sans-serif;

          .img {
            width: 25px;
            height: 25px;
            border-radius: 100%;
            overflow: hidden;
            margin-right: 10px;

            img {
              width: 100%;
              height: auto;
            }
          }

          a {
            text-decoration: none;
            color: $link;
          }
        }

        .info {
          margin-left: 5px;

          .info-flex {
            display: flex;
            align-items: center;
            justify-content: flex-start;
            flex-wrap: wrap;
            margin-bottom: 5px;

            svg {
              color: #3b3b3b;
              padding-right: 10px;
            }
          }

          .info-content {
            margin-left: 35px;
            margin-top: 10px;

            .info-date {
              color: #666;
              font-family: 'Courier New', Courier, monospace;
            }

            .info-degree,
            .info-gpa {
              font-family: Verdana, Geneva, Tahoma, sans-serif;
              margin-bottom: 10px;
            }

            .info-relevants {
              box-sizing: border-box;
              margin-top: 15px;

              .title {
                margin: 15px 0;
              }

              .table {
                tr {
                  transition: all 150ms ease-in-out;

                  &:hover {
                    background-color: rgba($color: $secondary, $alpha: 0.1);
                  }
                }

                thead {
                  background-color: #ededed;

                  tr:hover {
                    background-color: inherit;
                  }
                }
              }
            }

            .cert{
              margin-top: 10px;
              a{
                text-decoration: underline;
              }
              
            }
            
            .info-desc {
              display: flex;
              flex-direction: column;
              justify-content: center;
              color: #3b3b3b;
              font-family: Verdana, Geneva, Tahoma, sans-serif;

              &>div {
                margin-bottom: 10px;
              }
            }
          }
        }

      }

    }
  }

  @media only screen and (max-width: 960px) {
    .data {
      padding: 20px 15px;
      .data-item {
        padding-left: 0;

        li {
          padding: 0;
        }

        .work {
          padding: 0px;

          .info {
            margin-left: 10px;

            .info-content {
              margin-top: 15px;
              margin-left: 0px;

              .info-relevants {
                position: relative;
                min-height: 500px;
                overflow: auto;

                // max-width: 200px !important;
                .table {
                  position: absolute;

                }
              }
            }
          }
        }
      }
    }
  }
}